

<template>
    <div class="setting-variable-page">
        <smart-action offset-target="variable-value">
            <global-variable-layout type="vertical" style="padding-bottom: 20px;">
                <global-variable
                    v-for="variable in usedList"
                    ref="used"
                    :type="variable.type"
                    :key="variable.id"
                    :data="variable" />
                <Empty
                    v-if="usedList.length < 1"
                    key="empty"
                    :title="$t('template.暂无引用的变量')"
                    style="height: 160px; max-width: 960px; background-color: #f0f1f5;" />
                <toggle-display
                    v-if="unusedList.length > 0"
                    :count="unusedList.length"
                    style="max-width: 960px; margin-top: 20px;">
                    <div style="margin-top: 20px;">
                        <global-variable
                            v-for="variable in unusedList"
                            ref="unused"
                            :type="variable.type"
                            :key="variable.id"
                            :data="variable" />
                    </div>
                </toggle-display>
            </global-variable-layout>
            <template #action>
                <div class="action-wraper">
                    <bk-button
                        theme="primary"
                        class="w120 mr10"
                        :loading="isSubmiting"
                        @click="handleGoExec">
                        {{ $t('template.执行') }}
                    </bk-button>
                    <bk-button
                        @click="handleCancle">
                        {{ $t('template.取消') }}
                    </bk-button>
                    <bk-button
                        v-if="hasHostVariable"
                        class="remove-all"
                        @click="handleRemoveAllInvalidHost">
                        {{ $t('template.移除无效主机') }}
                    </bk-button>
                </div>
            </template>
        </smart-action>
        <back-top />
        <element-teleport v-if="planName">
            <div style="font-size: 12px; color: #63656e;">（{{ planName }}）</div>
        </element-teleport>
    </div>
</template>
<script>
    import _ from 'lodash';
    import I18n from '@/i18n';
    import TaskExecuteService from '@service/task-execute';
    import TaskPlanService from '@service/task-plan';
    import { findUsedVariable } from '@utils/assist';
    import GlobalVariableLayout from '@components/global-variable/layout';
    import GlobalVariable from '@components/global-variable/edit';
    import ToggleDisplay from '@components/global-variable/toggle-display';
    import BackTop from '@components/back-top';
    
    export default {
        name: '',
        components: {
            GlobalVariableLayout,
            GlobalVariable,
            ToggleDisplay,
            BackTop,
        },
        data () {
            return {
                isLoading: true,
                isSubmiting: false,
                hasHostVariable: false,
                usedList: [],
                unusedList: [],
                planName: '',
            };
        },
        computed: {
            isSkeletonLoading () {
                return this.isLoading;
            },
        },
        created () {
            this.taskId = this.$route.params.id;
            this.templateId = this.$route.params.templateId;
            this.isDebugPlan = Boolean(this.$route.params.debug);
            this.fetchData();
        },
        methods: {
            /**
             * @desc 获取执行方案数据
             */
            fetchData () {
                const serviceHandler = this.isDebugPlan
                    ? TaskPlanService.fetchDebugInfo
                    : TaskPlanService.fetchPlanDetailInfo;
                serviceHandler({
                    id: this.taskId,
                    templateId: this.templateId,
                }, {
                    permission: 'page',
                }).then((plan) => {
                    const {
                        name,
                        stepList,
                        variableList,
                    } = plan;
                    this.planName = name;
                    const planStepList = stepList.filter(step => step.enable === 1);
                    const usedVariableNameMap = findUsedVariable(planStepList).reduce((result, item) => {
                        result[item] = true;
                        return result;
                    }, {});
                    
                    // 执行方案中的步骤使用了得变量
                    const usedList = [];
                    // 未被使用的变量
                    const unusedList = [];
                    variableList.forEach((variable) => {
                        if (usedVariableNameMap[variable.name]) {
                            usedList.push(variable);
                        } else {
                            unusedList.push(variable);
                        }
                    });
                    this.usedList = Object.freeze(usedList);
                    this.unusedList = Object.freeze(unusedList);
                    this.hasHostVariable = _.findIndex(variableList, variable => variable.isHost) > -1;
                })
                    .catch((error) => {
                        if ([
                            1243009,
                            400,
                        ].includes(error.code)) {
                            setTimeout(() => {
                                this.$router.push({
                                    name: 'taskList',
                                });
                            }, 3000);
                        }
                    })
                    .finally(() => {
                        this.isLoading = false;
                    });
            },
            /**
             * @desc 开始执行
             */
            handleGoExec () {
                const validateQueue = [];
                if (this.$refs.used) {
                    this.$refs.used.forEach((item) => {
                        validateQueue.push(item.validate());
                    });
                }
                if (this.$refs.unused) {
                    this.$refs.unused.forEach((item) => {
                        validateQueue.push(item.validate());
                    });
                }
                this.isSubmiting = true;
                Promise.all(validateQueue)
                    .then(taskVariables => TaskExecuteService.taskExecution({
                        taskId: this.taskId,
                        taskVariables: taskVariables.map(({
                            id,
                            name,
                            type,
                            value,
                            targetValue,
                        }) => ({
                            id,
                            name,
                            type,
                            value,
                            targetValue,
                        })),
                    }).then(({ taskInstanceId }) => {
                        this.$bkMessage({
                            theme: 'success',
                            message: I18n.t('template.执行成功'),
                        });
                        window.changeAlert = false;
                        this.$router.push({
                            name: 'historyTask',
                            params: {
                                id: taskInstanceId,
                            },
                            query: {
                                from: 'planList',
                            },
                        });
                    }))
                    .catch(() => {
                        this.isSubmiting = false;
                    });
            },
            /**
             * @desc 取消执行
             */
            handleCancle () {
                this.routerBack();
            },
            /**
             * @desc 一键移除所有无效主机
             */
            handleRemoveAllInvalidHost () {
                this.$refs.used && this.$refs.used.forEach(item => item.removeAllInvalidHost());
                this.$refs.unused && this.$refs.unused.forEach(item => item.removeAllInvalidHost());
            },
            /**
             * @desc 路由回退
             */
            routerBack () {
                const { from } = this.$route.query;
                if (from === 'debugPlan') {
                    this.$router.push({
                        name: 'debugPlan',
                        params: {
                            id: this.templateId,
                        },
                    });
                    return;
                }
                if (from === 'viewPlan') {
                    this.$router.push({
                        name: 'viewPlan',
                        params: {
                            templateId: this.templateId,
                        },
                        query: {
                            viewPlanId: this.taskId,
                        },
                    });
                    return;
                }
                this.$router.push({
                    name: 'planList',
                    query: {
                        viewTemplateId: this.templateId,
                        viewPlanId: this.taskId,
                    },
                });
            },
        },
    };
</script>
<style lang='postcss'>
    .setting-variable-page {
        .action-wraper {
            display: flex;
            align-items: center;
            width: 960px;

            .remove-all {
                margin-left: 40px;
            }
        }
    }
</style>
